@import url(https://fonts.google.com/specimen/Bodoni+Moda?preview.text=EAT%20@%20HOME&preview.text_type=custom&sidebar.open=true&selection.family=Bodoni+Moda);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: antiquewhite;
  width: auto;
  align-items: center;
}

/* used globally */

h1 {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
  background-color: rgb(197, 58, 36);
}
h2 {
  max-width: 1200px;
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
  color: white;
  font-family: "Bodoni Moda", serif;
  font-size: 100px;
}
h3 {
  padding-left: 30px;
  display: flex;
  align-items: flex-start;
  font-size: 25px;
}

h4 {
  color: black;
  font-family: "Bodoni Moda", serif;
  font-size: 100px;
  padding-left: 20px;
  margin: 20px;
}

.navbar {
  color: whitesmoke !important;
  background-color: rgb(197, 58, 36);
  font-family: "Bodoni Moda", serif;
  display: flex;
  justify-content: space-around;
  -webkit-text-decoration-color: whitesmoke;
          text-decoration-color: whitesmoke;
  height: 40px;
  padding-top: 30px;
  width: auto;
}

.nav a {
  color: whitesmoke;
  text-decoration: none;
  font-size: 20px;
}

.text {
  color: black;
  text-align: center;
}

.info-text {
  display: flex;
  /* justify-content: center; */
  margin-left: 20px;
  width: 600px;
  text-align: left;
}

.feature_divider {
  width: 100%;
  margin-bottom: 20px;
  display: block;
  border: 0;
  border-color: #aaaaaa;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

.column {
  margin-top: 8px;
  vertical-align: middle;
}

.row {
  display: flex;
  flex-wrap: wrap;
  padding: 50px 4px;
  margin: 20px 20px 20px 20px;
}

.button {
  background-color: rgb(197, 58, 36);
  height: 40px;
  width: 80px;
  margin-top: 20px;
  font-size: 20px;
  margin-left: 20px;
}

.box {
  height: 200px;
  width: 400px;
}

/* CompTemp page styling */
.reviews-header {
  display: -flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 30px;
  margin: 30px;
  padding-bottom: 300px;
}
.img {
  margin: 0px;
}
.temp-image {
  position: left;
}

.button-pos {
  display: flex;
  margin-left: 40px;
}

.compTemp-items {
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
}

.compTemp-image {
  padding-left: 20px;
  height: 400px;
  width: 400px;
}

.review-box {
  padding-left: 30px;
}

/* home page styling */
.header-image {
  position: relative;
  width: 100%;
  z-index: -1;
  padding-bottom: 30px;
}
.header-image img {
  width: 100%;
}

h1 span {
  color: white;
  font: bold 24px/45px Helvetica, Sans-Serif;
  letter-spacing: -1px;
  background: none;
  text-align: center;
  font-size: 100px;
  font-family: "Bodoni Moda", serif;
  margin-bottom: 30px;
  background-color: rgb(197, 58, 36);
}

.bold-text {
  font-weight: bold;
  font-size: 40px;
}

/* InfoTile styling */
.c-image {
  border-color: rgb(197, 58, 36);
  position: relative;
  vertical-align: middle;
  object-fit: fill;
  width: 300px;
  justify-content: space-between;
  margin: 20px 20px 20px 20px;
}

.c-image img {
  width: 100%;
}

/*  About */
.about-image {
  border-style: groove;
  border-width: 6px;
  border-color: rgb(197, 58, 36);
  padding: 30px;
  margin: 20px;
}

.box-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  font-family: "Cabin Sketch", cursive;
  font-size: 30px;
  font-weight: bold;
  background-color: rgb(197, 58, 36, 0.5);
  height: auto;
  width: 100%;
}

/* Groceries/Kits/Meals styling */
.page-title {
  height: 60px;
  width: 300px;
  color: black;
  font-family: "Bodoni Moda", serif;
  font-size: 40px;
  padding: 30px;
  margin-bottom: 0px 20px 20px 20px;
}
.column {
  display: flex;
  flex: 50% 1;
  margin: 0px 20px 0px 0px;
}

.about {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 20px;
  padding-top: 20px;
}
.profile-image {
  padding-left: 20px;
  height: 400px;
  width: 400px;
}
.profile-items {
  padding-top: 0px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 40px;
}
.info-tile {
  width: 150px;
}
.test {
  border: rgb(197, 58, 36);
  border-width: 4px;
  border-style: solid;
  color: black;
  margin: 2rem;
}
.save {
  border: rgb(197, 58, 36);
  border-width: 2px;
  border-style: solid;
  font-family: "Cabin Sketch", cursive;
}

.save-items {
  display: flex;
  justify-content: center;
  padding: 40px;
}

.line-itmes {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.line-header {
  font-size: 30px;
}

.logout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 400px;
}

.hooks-button {
    cursor: pointer;
    display: flex;
    justify-content: center;
    font-size: 1.3em;
    /* box-sizing: content-box; */
    margin: 20px auto 0px;
    width: 20%;
    padding: 15px 20px;
    border-radius: 24px;
    border-color: transparent;
    background-color: white;
    /* box-shadow: 0px 16px 60px rgba(78, 79, 114, 0.1); */
    box-shadow: 0px 16px 60px rgba(78, 79, 114, 0.08);
    position: relative;
  }
  
  .buttonText {
    color: #4285f4;
    font-weight: bolder;
  }
  
  .icon {
    height: 25px;
    width: 25px;
    margin-right: 0px;
    position: absolute;
    left: 30px;
    align-items: center;
  }
